<template>
  <div>
    <page-title title="Rubix Apps">
      <template v-slot:spinner>
        <v-progress-circular v-if="isFetchingMRBConfig" indeterminate size="20"></v-progress-circular>
      </template>
    </page-title>

    <div v-if="!isFetchingMRBConfig">
      <div v-if="isMaster && !isSlaveModeActive">
        <v-row align="center" justify="center">
          <v-btn-toggle v-model="toggleMasterSlave" rounded>
            <v-btn small :value="1"> Master View </v-btn>
            <v-btn small :value="2">Device: {{
              deviceInfo && deviceInfo.device_name ? deviceInfo.device_name : 'Slave View'
            }}</v-btn>
          </v-btn-toggle>
        </v-row>
        <v-row>
          <v-col cols="12" v-if="!isMqttRestBridgeConfigLoaded && isFetchingMRBConfig">
            <v-skeleton-loader type="table-heading, list-item-two-line, image, table-tfoot"></v-skeleton-loader>
          </v-col>
          <v-col cols="12" v-if="!isFetchingMRBConfig && isMqttRestBridgeConfigLoaded && toggleMasterSlave == 2">
            <apps-grid></apps-grid>
          </v-col>
          <v-col cols="12" v-if="!isFetchingMRBConfig && isMqttRestBridgeConfigLoaded && toggleMasterSlave == 1">
            <slave-apps></slave-apps>
          </v-col>
        </v-row>
      </div>
      <div v-else>
        <v-col cols="12" v-if="isFetchingMRBConfig">
          <v-skeleton-loader type="table-heading, image, image, image, table-tfoot"></v-skeleton-loader>
        </v-col>
        <v-col cols="12" v-if="!isFetchingMRBConfig && isMqttRestBridgeConfigLoaded">
          <apps-grid></apps-grid>
        </v-col>
      </div>
    </div>
  </div>
</template>
<script>
import AppsGrid from './AppsGrid';
import SlaveApps from './SlaveApps';
import * as rubixService from '../../services/rubixService';
import * as mqttRestBridgeService from '../../services/mqttRestBridgeService';
import PageTitle from '../Common/PageTitle.vue';
const MASTER_VIEW = 1;
const SLAVE_VIEW = 2;
export default {
  name: 'rubix-apps',
  data() {
    return {
      toggleMasterSlave: MASTER_VIEW,
      isFetchingMRBConfig: false,
      deviceInfo: {},
    };
  },
  components: {
    AppsGrid,
    PageTitle,
    SlaveApps,
  },
  created() {
    this.isFetchingMRBConfig = true;
    this.getDeviceInfo();
    mqttRestBridgeService
      .fetchMRBListener()
      .then(data => {
        this.$store.commit('changeMqttRestBridgeConfig', data);
      })
      .finally(() => {
        this.isFetchingMRBConfig = false;
      });
  },
  methods: {
    getDeviceInfo() {
      rubixService.fetchPlatInfo().then(data => {
        this.deviceInfo = data;
      });
    },
  },
  computed: {
    isMqttRestBridgeConfigLoaded() {
      return Object.keys(this.$store.state.mqttRestBridgeConfig || {}).length > 0;
    },
    isSlaveModeActive() {
      return this.$store.state.isSwitchedSlave;
    },
    isMaster() {
      return this.$store.state.isMaster;
    },
  },
};
</script>
