<template>
  <v-card>
    <v-toolbar flat>
      <grid-toolbar :hide-export-data="true" :items="selectedItems" toolbarTitle="Rubix Apps">
        <template v-slot:customAction>
          <span>
            <v-btn
              :disabled="isDeletingDownloadState"
              @click="() => bulkDownloadApps(selectedItems)"
              x-small
              color="downloadColor"
              class="btn-height-22 white--text"
            >
              Download Apps
              <v-icon x-small right dark> mdi-download </v-icon>
            </v-btn>
            <v-progress-circular
              v-if="showDownloadProgressIcon"
              :width="2"
              :size="15"
              color="white"
              indeterminate
            ></v-progress-circular>
            <v-icon v-if="showCheckDownloadIcon" x-medium dark> mdi-check</v-icon>
          </span>
        </template>
      </grid-toolbar>
    </v-toolbar>
    <v-data-table
      show-select
      item-key="service"
      :headers="headers"
      :items="appDetails"
      :single-select="false"
      v-model="selectedItems"
      :loading="isFetchingAppDetails"
      :footer-props="tableProps.FOOTER_PROPS"
      :itemsPerPage="tableProps.ITEMS_PER_PAGE"
    >
      <template v-slot:[`item.state`]="{ item }">
        <app-status-chip :state="item.state"></app-status-chip>
      </template>
      <template v-slot:[`item.restart`]="{ item }">
        <v-tooltip bottom>
          <template v-slot:activator="{ on, attrs }">
            <v-btn
              @click="() => toggleDialog(dialogNames.restartService, item)"
              icon
              v-if="item.isUninstallable"
              small
              color="nubeGreen"
              v-bind="attrs"
              v-on="on"
            >
              <v-icon dark> mdi-restart </v-icon>
            </v-btn>
          </template>
          <span>Restart Service</span>
        </v-tooltip>
      </template>
      <template v-slot:[`item.actions`]="{ item }">
        <span>
          <v-tooltip bottom>
            <template v-slot:activator="{ on, attrs }">
              <v-btn
                @click="() => toggleDialog(dialogNames.install, item)"
                icon
                small
                color="downloadColor"
                v-bind="attrs"
                v-on="on"
              >
                <v-icon small dark> mdi-download </v-icon>
              </v-btn>
            </template>
            <span>Download</span>
          </v-tooltip>

          <v-tooltip bottom>
            <template v-slot:activator="{ on, attrs }">
              <v-btn
                v-bind="attrs"
                v-on="on"
                @click="() => toggleDialog(dialogNames.upload, item)"
                icon
                small
                color="nubeYellow"
              >
                <v-icon small dark> mdi-upload </v-icon>
              </v-btn>
            </template>
            <span>Upload</span>
          </v-tooltip>

          <v-tooltip bottom>
            <template v-slot:activator="{ on, attrs }">
              <v-btn
                v-if="item.isUninstallable"
                @click="() => toggleDialog(dialogNames.uninstall, item)"
                icon
                small
                color="error"
                v-bind="attrs"
                v-on="on"
              >
                <v-icon small dark> mdi-delete </v-icon>
              </v-btn>
            </template>
            <span>Delete App</span>
          </v-tooltip>
          <v-tooltip bottom>
            <template v-slot:activator="{ on, attrs }">
              <v-btn
                v-bind="attrs"
                v-on="on"
                :href="item.downloadUrl"
                target="_blank"
                tar
                v-if="item.downloadUrl"
                icon
                small
              >
                <v-icon small dark> mdi-zip-box</v-icon>
              </v-btn>
            </template>
            <span>Download ZIP</span>
          </v-tooltip>
        </span>
      </template>

      <template v-slot:[`item.configFile`]="{ item }">
        <v-tooltip bottom v-if="item.app_type == 'PythonApp' || item.app_type == 'FrontendApp'">
          <template v-slot:activator="{ on, attrs }">
            <v-btn
              @click="() => toggleDialog(dialogNames.editConfig, item)"
              icon
              small
              color="blue"
              v-bind="attrs"
              v-on="on"
            >
              <v-icon small dark> mdi-pencil </v-icon>
            </v-btn>
          </template>
          <span>Edit Config File</span>
        </v-tooltip>
        <v-tooltip bottom v-if="item.app_type == 'PythonApp' || item.app_type == 'FrontendApp'">
          <template v-slot:activator="{ on, attrs }">
            <v-btn
              @click="() => downloadConfig(item)"
              icon
              small
              color="downloadColor"
              v-bind="attrs"
              v-on="on"
            >
              <v-icon small dark> mdi-download </v-icon>
            </v-btn>
          </template>
          <span>Download Config File</span>
        </v-tooltip>
        <v-tooltip bottom v-if="item.app_type == 'PythonApp' || item.app_type == 'FrontendApp'">
          <template v-slot:activator="{ on, attrs }">
            <v-btn
              @click="() => toggleDialog(dialogNames.deleteAppConfig, item)"
              icon
              small
              color="error"
              v-bind="attrs"
              v-on="on"
            >
              <v-icon small dark> mdi-delete </v-icon>
            </v-btn>
          </template>
          <span>Delete Configuration</span>
        </v-tooltip>
      </template>

      <template v-slot:[`item.databaseOperations`]="{ item }">
        <v-tooltip bottom>
          <template v-slot:activator="{ on, attrs }">
            <v-btn
              @click="() => downloadAppData(item)"
              icon
              small
              color="downloadColor"
              v-bind="attrs"
              v-on="on"
            >
              <v-icon small dark> mdi-download </v-icon>
            </v-btn>
          </template>
          <span>Download Database</span>
        </v-tooltip>
        <v-tooltip bottom>
          <template v-slot:activator="{ on, attrs }">
            <v-btn
              @click="() => toggleDialog(dialogNames.deleteDatabase, item)"
              icon
              small
              color="error"
              v-bind="attrs"
              v-on="on"
            >
              <v-icon small dark> mdi-delete </v-icon>
            </v-btn>
          </template>
          <span>Delete Database</span>
        </v-tooltip>
      </template>
    </v-data-table>
    <confirm-dialog
      :is-open="dialog[dialogNames.uninstall]"
      :on-close="() => closeDialog(dialogNames.uninstall)"
      :on-confirm="() => uninstall(dialogNames.uninstall)"
      :loading="isUninstallingApp"
      dialog-text="Do you want to uninstall the app?"
    ></confirm-dialog>

    <confirm-dialog
      :is-open="dialog[dialogNames.restartService]"
      :on-close="() => closeDialog(dialogNames.restartService)"
      :on-confirm="() => restartService()"
      :loading="isRestartingService"
      dialog-text="Are you sure want to restart the app?"
    ></confirm-dialog>

    <confirm-dialog
      :is-open="dialog[dialogNames.deleteDatabase]"
      :on-close="() => closeDialog(dialogNames.deleteDatabase)"
      :on-confirm="() => deleteDatabase(dialogNames.deleteDatabase)"
      :loading="isDeletingDatabase"
      dialog-text="Do you want to delete the app's database?"
    ></confirm-dialog>

    <confirm-dialog
      :is-open="dialog[dialogNames.deleteAppConfig]"
      :on-close="() => closeDialog(dialogNames.deleteAppConfig)"
      :on-confirm="() => deleteAppConfigFile()"
      :loading="isDeletingConfig"
      dialog-text="Do you want to delete the app's config file?"
    ></confirm-dialog>

    <v-dialog persistent v-model="dialog[dialogNames.upload]" max-width="500px">
      <apps-upload-multi-step-view
        v-if="dialog[dialogNames.upload]"
        :app-details="selectedApp"
        :close-dialog="syncAppDetails => closeDialog(dialogNames.upload, syncAppDetails)"
      ></apps-upload-multi-step-view>
    </v-dialog>

    <v-dialog presistent v-model="dialog[dialogNames.editConfig]" max-width="500px">
      <apps-edit-config
        v-if="dialog[dialogNames.editConfig]"
        :close-dialog="syncAppDetails => closeDialog(dialogNames.editConfig, syncAppDetails)"
        :app-detail="selectedApp"
      ></apps-edit-config>
    </v-dialog>

    <v-dialog persistent v-model="dialog[dialogNames.install]" max-width="500px">
      <apps-download-multi-step-view
        v-if="dialog[dialogNames.install]"
        :delete-download-state="deleteDownloadState"
        :current-download-state="currentDownloadState"
        :fetch-download-app-state="fetchDownloadAppState"
        :clear-download-app-state-interval="clearDownloadAppStateInterval"
        :app-details="selectedApp"
        :close-dialog="syncAppDetails => closeDialog(dialogNames.install, syncAppDetails)"
      ></apps-download-multi-step-view>
    </v-dialog>

    <v-dialog :persistent="isBulkDownloadAppPersistent" v-model="dialog[dialogNames.bulkDownload]" max-width="500px">
      <apps-bulk-download-multi-step-view
        v-if="remountDialog[dialogNames.bulkDownload]"
        :current-download-state="currentDownloadState"
        :selected-apps="selectedItems"
        :fetch-download-app-state="fetchDownloadAppState"
        :app-download-states="appDownloadStates"
        :clear-selected-apps="clearSelectedApps"
        :delete-download-state="deleteDownloadState"
        :clear-download-app-state-interval="clearDownloadAppStateInterval"
        :close-dialog="(syncAppDetails, config) => closeDialog(dialogNames.bulkDownload, syncAppDetails, config)"
      ></apps-bulk-download-multi-step-view>
    </v-dialog>
  </v-card>
</template>

<script>
import JsZip from 'jszip';
import GridToolbar from '../Common/GridToolbar';
import AppsEditConfig from './appsEditConfig';
import AppsDownloadMultiStepView from './AppsDownloadMultiStep';
import AppsUploadMultiStepView from './AppsUploadMultiStepView';
import AppStatusChip from '../../components/Services/AppStatusChip';
import AppsBulkDownloadMultiStepView from './AppsBulkDownloadMultiStep';
import ConfirmDialog from '../Common/ConfirmDialog';
import * as appsService from '../../services/appsService';
import { RUBIX_APP_STATUS_CONTROL_ACTIONS } from '../../constants/appConstant';
import { DATA_TABLE_PROPS } from '../../constants/appConstant';

const DOWNLOAD_IN_PROGRESS = 'DOWNLOAD_IN_PROGRESS';
const DOWNLOAD_COMPLETE = 'DOWNLOAD_COMPLETE';

export default {
  name: 'systemServices',
  components: {
    AppsEditConfig,
    GridToolbar,
    AppsDownloadMultiStepView,
    ConfirmDialog,
    AppStatusChip,
    AppsUploadMultiStepView,
    AppsBulkDownloadMultiStepView,
  },
  data() {
    const dialogNames = {
      install: 'install',
      uninstall: 'uninstall',
      upload: 'upload',
      deleteDatabase: 'deleteDatabase',
      editConfig: 'editConfig',
      deleteAppConfig: 'deleteAppConfig',
      bulkDownload: 'bulkDownload',
      restartService: 'restartService',
    };
    return {
      selectedItems: [],
      isRestartingService: false,
      refreshTime: 5000,
      singleDownloadRefreshTIme: 1000,
      appDownloadStates: [],
      selectedApp: null,
      dialogNames: dialogNames,
      isFetchingAppDetails: false,
      isBulkDownloadAppPersistent: true,
      isDeletingConfig: false,
      isDeletingDatabase: false,
      isUninstallingApp: false,
      currentDownloadState: null,
      fetchDownloadStateInterval: null,
      isDeletingDownloadState: false,
      dialog: {
        [dialogNames.install]: false,
        [dialogNames.restartService]: false,
        [dialogNames.upload]: false,
        [dialogNames.uninstall]: false,
        [dialogNames.deleteDatabase]: false,
        [dialogNames.editConfig]: false,
        [dialogNames.deleteAppConfig]: false,
        [dialogNames.bulkDownload]: false,
      },
      remountDialog: {
        [dialogNames.bulkDownload]: true,
      },
      headers: [
        {
          text: 'Label',
          align: 'start',
          value: 'display_name',
        },
        {
          text: 'Name',
          align: 'start',
          value: 'service',
        },
        {
          text: 'Status',
          align: 'center',
          value: 'state',
        },
        {
          text: 'Restart',
          align: 'center',
          sortable: false,
          value: 'restart',
        },
        {
          text: 'Installed Version',
          align: 'center',
          value: 'installedVersion',
          sortable: true,
        },
        {
          text: 'Latest Available Version',
          align: 'center',
          value: 'latest_version',
        },
        {
          text: 'Actions',
          align: 'center',
          value: 'actions',
        },
        {
          text: 'Config File',
          align: 'center',
          value: 'configFile',
        },
        {
          text: 'Database',
          align: 'end',
          value: 'databaseOperations',
        },
      ],
      tableProps: {...DATA_TABLE_PROPS},

    };
  },
  computed: {
    appDetails() {
      return this.formatAppDetails(this.$store.state.appDetails);
    },
    setting() {
      return this.$store.state.setting;
    },
    showCheckDownloadIcon() {
      return this.currentDownloadState === DOWNLOAD_COMPLETE;
    },
    showDownloadProgressIcon() {
      return this.currentDownloadState === DOWNLOAD_IN_PROGRESS;
    },
  },
  mounted() {
    this.fetchDownloadAppState();
  },
  created() {
    this.fetchAppDetails();
  },
  destroyed() {
    this.fetchDownloadStateInterval = null;
  },
  methods: {
    closeDialog(dialogName, syncAppDetails, config = {}) {
      this.dialog[dialogName] = false;
      if (config.unmount && this.remountDialog[dialogName]) {
        this.remountDialog[dialogName] = false;
        setTimeout(() => {
          this.remountDialog[dialogName] = true;
        }, 500);
      }
      syncAppDetails && this.fetchAppDetails(true);
    },
    openDialog(dialogName) {
      this.dialog[dialogName] = true;
    },
    toggleDialog(dialogName, item) {
      if (!this.dialog[dialogName]) {
        switch (dialogName) {
          case this.dialogNames.install:
            if (this.currentDownloadState) {
              return this.$toasted.show('Please resolve bulk download apps in the top left first.', { type: 'info' });
            }
        }
      }
      this.dialog[dialogName] = !this.dialog[dialogName];

      if (this.dialog[dialogName]) {
        this.selectedApp = item;
      } else {
        this.selectedApp = null;
      }
    },
    formatAppDetails(appDetails) {
      return appDetails.map(detail => ({
        ...detail,
        activeLabel: detail.status ? 'Running' : 'Stopped',
        isUninstallable: detail.is_installed,
        installedVersion: detail.version ? detail.version : '-',
        downloadUrl: detail.browser_download_url ? detail.browser_download_url : null,
        updatedAt: detail.date_since ? new Date(detail.date_since).toLocaleDateString() : '-',
      }));
    },
    fetchAppDetails(syncInstalledApp = false) {
      this.isFetchingAppDetails = true;
      return appsService
        .fetchApps()
        .then(r => {
          this.$store.commit('changeAppDetails', r);
          syncInstalledApp && this.fetchAllInstalledApps();
        })
        .finally(() => {
          this.isFetchingAppDetails = false;
        });
    },
    fetchAllInstalledApps() {
      appsService
        .fetchAllInstalledApps()
        .then(r => {
          this.$store.commit('changeInstalledServices', r);
        })
        .catch(e => {});
    },
    deleteDatabase() {
      this.isDeletingDatabase = true;
      appsService
        .deleteAppDataService(this.selectedApp.service)
        .then(() => {
          this.closeDialog(this.dialogNames.deleteDatabase, true);
        })
        .catch(() => {
          this.closeDialog(this.dialogNames.deleteDatabase);
        })
        .finally(() => {
          this.isDeletingDatabase = false;
        });
    },
    uninstall(dialogName) {
      this.isUninstallingApp = true;
      appsService
        .uninstallApp(this.selectedApp.service)
        .then(() => {
          this.fetchAppDetails(true);
          this.closeDialog(this.dialogNames.uninstall, true);
        })
        .catch(() => {
          this.closeDialog(this.dialogNames.uninstall);
        })
        .finally(() => {
          this.isUninstallingApp = false;
        });
    },
    getFileName(serviceName) {
      const d = new Date();
      const datestring = `${d.getDate()}-${
        d.getMonth() + 1
      }-${d.getFullYear()}T${d.getHours()}:${d.getMinutes()}:${d.getSeconds()}`;
      return `${serviceName}_${datestring}`;
    },
    downloadAppData(appDetail) {
      const serviceName = appDetail.service;

      appsService
        .downloadAppData(serviceName, this.setting)
        .then(async blobData => {
          const zip = new JsZip();
          await zip.loadAsync(blobData);
          const blob = await zip.generateAsync({ type: 'blob' });
          const date = new Date();
          const filename = this.getFileName(serviceName);
          const link = document.createElement('a');
          link.href = window.URL.createObjectURL(blob);
          link.setAttribute('download', `${filename}.zip`);
          link.click();
          this.$toasted.show(`${appDetail.service} app data is exported.`);
        })
        .catch(err => {
          const reader = new FileReader();
          reader.addEventListener('loadend', e => {
            const err = JSON.parse(e.target.result);
            if (err && err.message) {
              this.$toasted.show(err.message, { type: 'error' });
            } else {
              this.$toasted.show(`Failed to export ${serviceName} app data`, { type: 'error' });
            }
          });
          reader.readAsText(err);

          return err;
        });
    },
    deleteAppConfigFile() {
      this.isDeletingConfig = true;
      const serviceName = this.selectedApp.service;
      const appType = this.selectedApp.app_type;

      appsService
        .deleteAppConfig(serviceName, appType)
        .then(data => {
          this.$toasted.show(`${serviceName} app config file is deleted`);
          this.closeDialog(this.dialogNames.deleteAppConfig);
        })
        .catch(err => {
          this.$toasted.show(`Failed to delete app config for ${serviceName}`, { type: 'error' });
        })
        .finally(() => {
          this.isDeletingConfig = false;
        });
    },
    downloadConfig(appDetail) {
      appsService.downloadAppConfig(appDetail.service, appDetail.app_type);
    },
    restartService() {
      this.isRestartingService = true;
      const serviceName = this.selectedApp.service;
      appsService
        .changeServiceStatus(RUBIX_APP_STATUS_CONTROL_ACTIONS.RESTART, serviceName)
        .then(() => {
          this.$toasted.show(`App restarted sucessfully.`);
          this.closeDialog(this.dialogNames.restartService);
        })
        .catch(() => {
          this.$toasted.show(`Failed to restart the app.`, { type: 'error' });
        })
        .finally(() => {
          this.isRestartingService = false;
        });
    },
    bulkDownloadApps() {
      this.openDialog(this.dialogNames.bulkDownload);
    },
    clearDownloadAppStateInterval() {
      clearInterval(this.fetchDownloadStateInterval);
      this.fetchDownloadStateInterval = null;
    },
    downloadStateInterval() {
      const refreshTime = this.dialog[this.dialogNames.install] ? this.singleDownloadRefreshTIme : this.refreshTime;

      if (!this.fetchDownloadStateInterval) {
        this.fetchDownloadStateInterval = setInterval(() => {
          this.fetchDownloadAppState();
        }, refreshTime);
      }
    },
    fetchDownloadAppState() {
      return appsService
        .fetchDownloadState()
        .then(data => {
          const { state: downloadState, services: appDownloadStates } = data;
          switch (downloadState) {
            case 'DOWNLOADING':
              this.currentDownloadState = DOWNLOAD_IN_PROGRESS;
              this.isBulkDownloadAppPersistent = false;
              if (!this.fetchDownloadStateInterval) {
                this.downloadStateInterval();
              }
              break;
            case 'CLEARED':
              this.currentDownloadState = null;
              this.clearDownloadAppStateInterval();
              break;
            case 'DOWNLOADED':
              this.currentDownloadState = DOWNLOAD_COMPLETE;
              this.isBulkDownloadAppPersistent = true;
              break;
          }

          if (Array.isArray(appDownloadStates)) {
            this.appDownloadStates = appDownloadStates;
          }

          return appDownloadStates;
        })
        .catch(err => {
          if (err && err.message) {
            this.currentDownloadState = null;
          }
          return err;
        });
    },
    clearSelectedApps() {
      this.appDownloadStates = [];
      this.selectedItems = [];
    },
    deleteDownloadState() {
      this.isDeletingDownloadState = true;
      return appsService
        .deleteDownloadState()
        .then(data => data)
        .catch(err => err)
        .finally(() => {
          this.fetchDownloadAppState();
          this.clearDownloadAppStateInterval();
          this.isDeletingDownloadState = false;
        });
    },
  },
};
</script>
