<template>
  <v-card>
    <v-card-title>Control Actions Status Report</v-card-title>
    <v-card-text>
      <v-list flat subheader dense class="text-left">
        <v-subheader>Failed apps</v-subheader>
        <template v-for="(app, i) in faliureApps">
          <v-list-item class="pl-5" :key="`failed-app-${i}`">
            <v-list-item-content>
              <v-list-item-title
                class="subtitle-2 font-weight-medium"
                v-text="`${app.displayName} | ${app.device_name} | Action: ${app.action}`"
              ></v-list-item-title>
              <p class="body-">
                <span>Message: </span> <span :title="app.error" class="red--text">{{ app.error }}</span>
              </p>
            </v-list-item-content>
          </v-list-item>
        </template>
      </v-list>
    </v-card-text>
  </v-card>
</template>
<script>
export default {
  name: 'slave-apps-control-action-report',
  props: ['controlActionErrorReport', 'closeDialog', 'slaveDevices', 'slaveAppDisplayNameMap'],
  computed: {
    successApps() {
      return this.controlActionErrorReport.success;
    },
    faliureApps() {
      return Object.keys(this.controlActionErrorReport).reduce((acc, serviceKey) => {
        let failureApps = this.controlActionErrorReport[serviceKey].failure;
        failureApps = failureApps.map(app => ({
          ...app,
          action: (app.action || '').toUpperCase(),
          displayName: this.slaveAppDisplayNameMap[app.service],
          ...((this.slaveDevices && this.slaveDevices[app.slaveId]) || {}),
        }));
        acc.push(...failureApps);
        return acc;
      }, []);
    },
  },
};
</script>
