<template>
  <v-card :loading="isLoadingSlaveApps || isFetchingAppData || isDownloadingApps">
    <v-toolbar flat>
      <v-toolbar-title>
        <span>Master Slave Apps</span>
        <tooltip-icon-button
          btnColor="default"
          btnIcon="mdi-sync"
          btnTooltip="Refresh master view"
          buttonSize="medium"
          :onClick="() => initialize()"
        >
        </tooltip-icon-button>
      </v-toolbar-title>
      <v-menu bottom left>
        <template v-slot:activator="{ on, attrs }">
          <v-btn icon v-bind="attrs" v-on="on">
            <v-icon :color="appliedFilterCount > 0 ? 'blue' : ''">mdi-filter</v-icon>
          </v-btn>
        </template>

        <v-list dense>
          <v-subheader>Filters</v-subheader>

          <v-list-item>
            <v-list-item-title>
              <v-checkbox class="mt-0" hide-details v-model="filters.isInstalled" label="Installed Apps"></v-checkbox>
            </v-list-item-title>
          </v-list-item>
        </v-list>
      </v-menu>

      <v-spacer></v-spacer>
      <v-btn
        x-small
        class="mr-2 btn-height-22 white--text"
        color="default"
        v-if="!!currentDownloadState"
        @click="() => (isClearDialog = true)"
      >
        Clear Update
        <v-icon x-small right> mdi-cellphone-erase </v-icon>
      </v-btn>
      <v-btn x-small class="btn-height-22 white--text" color="pink" @click="onBulkUpdate">
        <v-progress-circular
          v-if="showDownloadProgressIcon"
          :width="2"
          :size="15"
          color="white"
          indeterminate
        ></v-progress-circular>
        <v-icon small v-if="showCheckDownloadIcon">mdi-check</v-icon>
        <span class="pl-2">Bulk Update</span>
        <v-icon small right>mdi-cellphone-arrow-down</v-icon>
      </v-btn>
    </v-toolbar>
    <v-spacer></v-spacer>
    <v-card-text>
      <v-container fluid>
        <v-row no-gutter class="mb-2">
          <v-col cols="3">
            <p class="mb-0 text-left" v-if="selectedServicesCount > 0">
              <span class="grey-text font-weight-medium caption">Selected Apps: </span>
              <span class="font-weight-medium">{{ selectedServicesCount }}</span>
            </p>
          </v-col>
          <v-col cols="6">
            <icon-button
              :key="buttonConfig.name"
              v-for="buttonConfig in actionButtons"
              btn-size="small"
              :disabled="
                selectedServicesCount == 0 || (currentControlAction && currentControlAction !== buttonConfig.action)
              "
              :on-click="() => handleControlAction(buttonConfig.action)"
              :button-title="buttonConfig.title"
              :loading="currentControlAction && currentControlAction == buttonConfig.action && isControlActionPending"
              :icon-key="buttonConfig.iconKey"
              :color="buttonConfig.color"
            ></icon-button>
          </v-col>
          <v-col cols="3"></v-col>
        </v-row>
        <v-tabs v-model="slaveTabs" :color="`${$vuetify.theme.dark ? '': 'nubeGreen'}`">
          <v-tab v-for="(slaveApps, index) in slaveAppsDetail" :key="index"> {{ slaveApps.display_name }} </v-tab>
        </v-tabs>
        <v-tabs-items v-model="slaveTabs">
          <v-tab-item v-for="(slaveApps, index) in slaveAppsDetail" :key="index">
            <v-row no-gutter>
              <v-col cols="12" lg="12" xl="12" md="12" sm="12" xs="12">
                <slave-app-grid
                  :slave-devices="slaveDevices"
                  :search-text="searchText"
                  :filters="filters"
                  :latest-version="slaveAppsLatestVersion[slaveApps.name]"
                  :onSelectItem="onSelectItem"
                  :search="gridSearchMap[slaveApps.name]"
                  :updateSearch="(search) => updateSearch(slaveApps.name, search)"
                  :key="`${recompute}-${slaveApps.name}`"
                  :name="`${recompute}-${slaveApps.name}`"
                  :selected-item="selectedItems[slaveApps.name]"
                  :slave-apps="slaveApps"
                  :slave-apps-data-items="slaveApps.dataItems"
                ></slave-app-grid>
              </v-col>
            </v-row>
          </v-tab-item>
        </v-tabs-items>
      </v-container>
      <v-row v-if="isLoadingSlaveApps">
        <v-col cols="12" v-if="isLoadingSlaveApps">
          <v-skeleton-loader type="table-heading, list-item-one-line, image"></v-skeleton-loader>
        </v-col>
      </v-row>

      <v-col cols="12" class="text-left" v-if="isFetchAppfailure">
        <v-alert v-if="fetchErrorMessage" class="mb-0 text-left" dense outlined type="error">
          {{ fetchErrorMessage }}
        </v-alert>
        <v-btn x-small color="primary" class="btn-height-22 white--text mt-8" @click="retryFetchApps">Click here to retry</v-btn>
      </v-col>
    </v-card-text>
    <v-dialog persistent v-model="dialog[dialogNames.bulkUpdate]" max-width="600px">
      <slave-apps-download-multi-step
        :app-versions="appVersions"
        :slave-devices="slaveDevices"
        :close-dialog="() => closeDialog(dialogNames.bulkUpdate)"
        :selected-apps="selectedItems"
        :clear-selected-apps="clearSelectedApps"
        :delete-download-state="deleteDownloadState"
        :current-download-state="currentDownloadState"
        :slave-apps-download-states="slaveAppsDownloadStates"
        :fetch-download-app-state="fetchDownloadAppState"
        :clear-download-app-state-interval="clearDownloadAppStateInterval"
      ></slave-apps-download-multi-step>
    </v-dialog>
    <v-dialog v-model="dialog[dialogNames.controlActionErrorReport]" max-width="600px">
      <slave-apps-control-action-report
        :slave-devices="slaveDevices"
        :slave-app-display-name-map="slaveAppDisplayNameMap"
        :close-dialog="
          () => {
            dialog[dialogNames.controlActionErrorReport] = false;
          }
        "
        :control-action-error-report="controlActionErrorReport"
      ></slave-apps-control-action-report>
    </v-dialog>
    <confirm-dialog
      :is-open="isClearDialog"
      :loading="isClearingUpdate"
      :on-close="() => (isClearDialog = false)"
      :on-confirm="onClearDownload"
      dialog-text="All progress will be lost. Are you sure?"
    ></confirm-dialog>
  </v-card>
</template>
<script>
import PageTitle from '../Common/PageTitle';
import SlaveAppGrid from './SlaveAppGrid';
import ConfirmDialog from '../Common/ConfirmDialog';
import * as slaveAppsUiService from '../../services/slaveAppsUiService';
import SlaveAppsDownloadMultiStep from './SlaveAppsDownloadMultiStep';
import * as slaveAppsService from '../../services/slaveAppsService';
import SlaveAppsControlActionReport from './SlaveAppsControlActionReport';
import * as slaveDeviceService from '../../services/slaveDevicesService';
import IconButton from '../Services/IconButton';
import TooltipIconButton from './TooltipIconButton.vue';

import { APP_DOWNLOAD_STATUSES, RUBIX_APP_STATUS_CONTROL_ACTIONS } from '../../constants/appConstant';

const controlActionErrorStates = {
  success: 'success',
  failure: 'failure',
  partial: 'partial',
};
const controlMessageStatesMap = {
  success: {
    type: 'success',
    duration: 1000,
    message: action => `Sucessfully applied action '${action}' all apps.`,
  },
  failure: {
    type: 'error',
    duration: 10000,
    message: action => `Failed to apply action '${action}' to all apps.`,
  },
  partial: {
    type: 'info',
    duration: 10000,
    message: action => `Partially applied action '${action}' to all apps.`,
  },
};
export default {
  name: 'slave-apps',
  components: {
    PageTitle,
    IconButton,
    SlaveAppGrid,
    SlaveAppsDownloadMultiStep,
    ConfirmDialog,
    SlaveAppsControlActionReport,
    TooltipIconButton,
  },
  data() {
    const dialogNames = {
      bulkUpdate: 'bulkUpdate',
      controlActionErrorReport: 'controlActionErrorReport',
    };
    const btnNames = {
      controlErrorState: null,
      startBtn: 'startBtn',
      restartBtn: 'restartBtn',
      stopBtn: 'stopBtn',
      enableBtn: 'enableBtn',
      disableBtn: 'disableBtn',
    };
    const btnConfig = {
      [btnNames.startBtn]: {
        name: btnNames.startBtn,
        title: 'Start',
        color: 'light-blue darken-2',
        iconKey: 'mdi-play-circle-outline',
        action: RUBIX_APP_STATUS_CONTROL_ACTIONS.START,
      },
      [btnNames.restartBtn]: {
        name: btnNames.restartBtn,
        title: 'Restart',
        color: 'nubeGreen',
        iconKey: 'mdi-restart',
        action: RUBIX_APP_STATUS_CONTROL_ACTIONS.RESTART,
      },
      [btnNames.stopBtn]: {
        name: btnNames.stopBtn,
        title: 'Stop',
        iconKey: 'mdi-stop-circle-outline',
        action: RUBIX_APP_STATUS_CONTROL_ACTIONS.STOP,
      },
      [btnNames.enableBtn]: {
        name: btnNames.enableBtn,
        title: 'Enable',
        color: 'teal darken-2',
        iconKey: 'mdi-check-circle-outline',
        action: RUBIX_APP_STATUS_CONTROL_ACTIONS.ENABLE,
      },
      [btnNames.disableBtn]: {
        name: btnNames.disableBtn,
        title: 'Disable',
        color: 'orange darken-2',
        iconKey: 'mdi-close-octagon',
        action: RUBIX_APP_STATUS_CONTROL_ACTIONS.DISABLE,
      },
    };
    const actionBtnKeys = [
      btnNames.startBtn,
      btnNames.stopBtn,
      btnNames.restartBtn,
      btnNames.enableBtn,
      btnNames.disableBtn,
    ];

    return {
      recompute: 0,
      slaveTabs: null,
      filters: {
        isInstalled: false,
      },
      gridSearchMap: {},
      slaveAppDisplayNameMap: {},
      slaveAppsLatestVersion: {},
      isFetchingSlaveApps: false,
      currentControlAction: null,
      isControlActionPending: false,
      statusControlErrors: [],
      isFetchAppfailure: false,
      isDownloadingApps: false,
      isClearDialog: false,
      isClearingUpdate: false,
      isLoadingSlaveApps: false,
      slaveAppsOnceLoaded: false,
      controlActionState: null,
      controlActionErrorReport: {},
      slaveReleaseList: null,
      fetchErrorMessage: '',
      searchText: '',
      isFetchingAppData: false,
      refreshTime: 5000,
      isDeletingDownloadState: false,
      currentDownloadState: null,
      slaveAppsDetailMap: {},
      slaveAppsDetail: [],
      selectedItems: {},
      appVersions: {},
      selectedAppVersion: {},
      slaveAppReleases: {},
      fetchDownloadAppStateInterval: null,
      slaveDevices: {},
      slaveAppsDownloadStates: {},
      dialogNames,
      dialog: {
        [dialogNames.bulkUpdate]: false,
        [dialogNames.controlActionErrorReport]: false,
      },
      actionButtons: actionBtnKeys.map(key => btnConfig[key]),
      remountDialog: {
        [dialogNames.bulkUpdate]: true,
      },
    };
  },
  mounted() {
    this.initialize();
  },
  destroyed() {
    this.fetchDownloadAppStateInterval = null;
  },
  computed: {
    showCheckDownloadIcon() {
      return this.currentDownloadState === APP_DOWNLOAD_STATUSES.DOWNLOAD_COMPLETE;
    },
    showDownloadProgressIcon() {
      return this.currentDownloadState === APP_DOWNLOAD_STATUSES.DOWNLOAD_IN_PROGRESS;
    },
    selectedServicesCount() {
      return Object.keys(this.selectedItems).reduce((acc, curr) => {
        acc += this.selectedItems[curr].length;
        return acc;
      }, 0);
    },
    appliedFilterCount() {
      return Object.keys(this.filters).reduce((acc, filterKey) => {
        if (this.filters[filterKey]) {
          acc++;
        }
        return acc;
      }, 0);
    },
  },
  methods: {
    retryFetchApps() {
      this.fetchErrorMessage = '';
      this.isFetchAppfailure = false;
      this.initialize();
    },
    async initialize() {
      this.isFetchingAppData = true;
      await Promise.all([this.fetchSlaveApps(true), this.fetchSlaveDevices()]).finally(() => {
        this.isFetchingAppData = false;
        this.recompute++;
      });
      await this.fetchSlaveAppsLatestVersion();
    },
    openDialog(dialogName) {
      this.dialog[dialogName] = true;
    },
    toggleDialog(dialogName, item) {
      this.dialog[dialogName] = !this.dialog[dialogName];

      if (this.dialog[dialogName]) {
        this.selectedApp = item;
      } else {
        this.selectedApp = null;
      }
    },
    fetchSlaveAppsLatestVersion() {
      return slaveAppsService.fetchLatestAppVersion().then(data => {
        this.slaveAppsLatestVersion = data;
      });
    },
    updateSearch(serviceName, searchText) {
      this.gridSearchMap[serviceName] = searchText;
    },
    fetchSlaveApps(fetchAppReleases = false) {
      if (this.isFetchingSlaveApps) {
        return;
      }
      if (!this.slaveAppsOnceLoaded) {
        this.isLoadingSlaveApps = true;
      }
      this.isFetchingSlaveApps = true;
      return slaveAppsService
        .fetch()
        .then(({ data = {} }) => {
          this.slaveAppsDetailMap = data;
          if (Object.keys(this.gridSearchMap).length === 0) {
            this.gridSearchMap = Object.keys(data).reduce((acc, key) => {
              acc[key] = '';
              return acc;
            }, {});
          }
          this.slaveAppDisplayNameMap = Object.keys(data).reduce((acc, key) => {
            acc[key] = data[key] ? data[key].display_name : key;
            return acc;
          }, {});
          this.slaveAppsDetail = Object.keys(data).map(slaveAppKey => data[slaveAppKey]);
          this.selectedItems = Object.keys(data).reduce((acc, curr) => {
            acc[curr] = [];
            return acc;
          }, {});
          this.slaveAppsOnceLoaded = true;
          if (fetchAppReleases) {
            this.fetchSlaveAppReleases();
          }
        })
        .catch(err => {
          if (err && err.message) {
            this.$toasted.show(err.message, { type: 'error' });
            this.fetchErrorMessage = err.message;
          }
          this.isFetchAppfailure = true;
        })
        .finally(() => {
          this.isLoadingSlaveApps = false;
          this.isFetchingSlaveApps = false;
        });
    },
    onSelectItem(serviceName, items) {
      this.selectedItems[serviceName] = items;
      this.selectedAppVersion = slaveAppsUiService.slaveReleaseList({
        releaseMap: this.appVersions,
        selectedItems: this.selectedItems,
      });
    },
    fetchSlaveDevices() {
      return slaveDeviceService
        .fetch()
        .then(slaveDevices => {
          this.slaveDevices = slaveDevices;
          this.fetchDownloadAppState();
        })
        .catch(err => {
          // this.$toasted.show('Error fetching slave devices', { type: 'error' });
        });
    },
    fetchSlaveAppReleases() {
      const serviceNames = Object.keys(this.slaveAppsDetailMap);

      if (!serviceNames.length) {
        return null;
      }

      return slaveAppsService
        .fetchAppsReleases(serviceNames)
        .then(appVersions => {
          this.appVersions = serviceNames.reduce((acc, serviceName, index) => {
            acc[serviceName] = appVersions[index];
            return acc;
          }, {});
        })
        .catch(err => {
          this.$toasted.show('Error fetching app releases');
        });
    },
    onBulkUpdate() {
      this.dialog[this.dialogNames.bulkUpdate] = true;
    },
    closeDialog(dialogName) {
      this.dialog[dialogName] = false;
    },
    startDownloadStateInterval() {
      if (!this.fetchDownloadAppStateInterval) {
        this.fetchDownloadAppStateInterval = setInterval(() => {
          this.fetchDownloadAppState();
        }, this.refreshTime);
      }
    },
    clearDownloadAppStateInterval() {
      clearInterval(this.fetchDownloadAppStateInterval);
      this.fetchDownloadAppStateInterval = null;
    },
    deleteDownloadState() {
      this.isDeletingDownloadState = true;
      const slaveUUIDs = Object.keys(this.slaveDevices);
      if (!(slaveUUIDs && slaveUUIDs.length > 0)) {
        return null;
      }
      return slaveAppsService.clearDownloadState(slaveUUIDs).finally(() => {
        this.fetchDownloadAppState();
        this.clearDownloadAppStateInterval();
        this.isDeletingDownloadState = false;
      });
    },
    fetchDownloadAppState() {
      const slaveUUIDs = Object.keys(this.slaveDevices);
      if (!(slaveUUIDs && slaveUUIDs.length > 0)) {
        return null;
      }
      return slaveAppsService
        .fetchDownloadState()
        .then(({ data: slaveAppsDownloadStates, globalState: downloadState }) => {
          switch (downloadState) {
            case 'DOWNLOADING':
              this.currentDownloadState = APP_DOWNLOAD_STATUSES.DOWNLOAD_IN_PROGRESS;
              // this.isBulkDownloadAppPersistent = false;
              if (!this.fetchDownloadAppStateInterval) {
                this.startDownloadStateInterval();
              }
              this.isDownloadingApps = true;

              break;
            case 'CLEARED':
              this.currentDownloadState = null;
              this.clearDownloadAppStateInterval();
              this.isDownloadingApps = false;

              break;
            case 'DOWNLOADED':
              this.currentDownloadState = APP_DOWNLOAD_STATUSES.DOWNLOAD_COMPLETE;
              // this.isBulkDownloadAppPersistent = true;
              this.clearDownloadAppStateInterval();
              this.isDownloadingApps = false;

              break;
          }
          this.slaveAppsDownloadStates = slaveAppsDownloadStates;
          Object.keys(this.slaveAppsDownloadStates).forEach(serviceName => {
            this.slaveAppsDownloadStates[serviceName].dataItems = this.slaveAppsDownloadStates[
              serviceName
            ].dataItems.map(data => this.appendSlaveDevice(data));
          });
          return slaveAppsDownloadStates;
        })
        .catch(err => {
          this.isDownloadingApps = false;
        });
    },
    clearSelectedApps() {
      this.slaveAppsDownloadStates = {};
      this.selectedItems = {};
      this.recompute++;
      this.fetchSlaveApps();
    },
    appendSlaveDevice(data) {
      let newData = { ...data };
      const slaveDevice = this.slaveDevices[data.slaveId];
      if (slaveDevice) {
        newData = {
          ...newData,
          ...Object.keys(slaveDevice).reduce((acc, curr) => {
            const key = 'slave_' + curr;
            acc[key] = slaveDevice[curr];
            return acc;
          }, {}),
        };
      }
      return newData;
    },
    onClearDownload() {
      this.isClearingUpdate = true;
      this.clearSelectedApps();
      this.deleteDownloadState().finally(() => {
        this.isClearDialog = false;
        this.isClearingUpdate = false;
      });
    },
    getControlErrorReport(data) {
      return Object.keys(data).reduce((acc, key) => {
        if (!acc[key]) {
          acc[key] = {
            success: [],
            failure: [],
          };
        }
        const dataItems = data[key].dataItems;
        acc[key].success = dataItems.filter(item => !item.error);
        acc[key].failure = dataItems.filter(item => item.error);
        return acc;
      }, {});
    },
    getControlActionState(errReport) {
      const errStates = Object.keys(errReport).reduce((acc, serviceKey) => {
        const success = errReport[serviceKey].success;
        const failure = errReport[serviceKey].failure;
        if (failure.length == 0) {
          acc.push(controlActionErrorStates.success);
        } else if (failure.length > 0 && success.length !== 0) {
          acc.push(controlActionErrorStates.partial);
        } else {
          acc.push(controlActionErrorStates.failure);
        }
        return acc;
      }, []);

      if (errStates.every(item => item === controlActionErrorStates.success)) {
        return controlActionErrorStates.success;
      } else if (errStates.every(item => item === controlActionErrorStates.failure)) {
        return controlActionErrorStates.failure;
      } else {
        return controlActionErrorStates.partial;
      }
    },
    openErrorReportDialog() {
      this.dialog[this.dialogNames.controlActionErrorReport] = true;
    },
    getToastMessageAction(status) {
      const actions = [];
      if (status === controlActionErrorStates.failure || status === controlActionErrorStates.partial) {
        actions.push({
          text: 'Click here to see Error Report.',
          onClick: (e, toastObject) => this.openErrorReportDialog(e, toastObject, status),
        });
      }
      return actions;
    },
    handleControlAction(action) {
      this.currentControlAction = action;
      this.isControlActionPending = true;
      const apps = Object.keys(this.selectedItems).reduce((acc, serviceKey) => {
        if (this.selectedItems[serviceKey] && this.selectedItems[serviceKey].length > 0) {
          acc.push(...this.selectedItems[serviceKey]);
        }
        return acc;
      }, []);
      return slaveAppsService
        .bulkControlApps(apps, action)
        .then(({ data }) => {
          const errorReport = this.getControlErrorReport(data);
          const controlActionState = this.getControlActionState(errorReport);
          const controlActionMessage = controlMessageStatesMap[controlActionState];
          this.$toasted.show(controlActionMessage.message(action), {
            type: controlActionMessage.type,
            duration: controlActionMessage.duration,
            action: this.getToastMessageAction(controlActionState),
          });
          this.controlActionErrorReport = errorReport;
          this.clearSelectedApps();
        })
        .catch(err => {})
        .finally(() => {
          this.currentControlAction = null;
          this.isControlActionPending = false;
        });
    },
  },
};
</script>
