<template>
  <v-card :loading="isFetchingConfig">
    <v-card-title>Edit Config</v-card-title>
    <v-card-text>
      <v-container>
        <v-jsoneditor
          v-if="isConfigJSON"
          :options="{ mode: 'code' }"
          v-model="config"
          :plus="false"
          height="400px"
        ></v-jsoneditor>
        <v-textarea
          v-if="!isConfigJSON"
          outlined
          name="input-7-4"
          label="Enter ENV config"
          v-model="config"
        ></v-textarea>
      </v-container>
    </v-card-text>
    <v-card-actions>
      <v-btn small text @click="closeDialog">Cancel</v-btn>
      <v-btn x-small class="mr-1 btn-height-22 white--text" color="primary" @click="saveConfig" :loading="isSavingConfig">Save</v-btn>
    </v-card-actions>
  </v-card>
</template>
<script>
import * as appsService from '../../services/appsService';
import * as slaveAppsService from '../../services/slaveAppsService';
import { APP_CONFIG_TYPES, JSON_CONFIG_TYPE } from '../../constants/appConstant';
export default {
  name: 'app-edit-config',
  props: ['appDetail', 'closeDialog', 'isSlaveApp'],
  data() {
    return {
      config: null,
      slaveId: null,
      apiService: null,
      isFetchingConfig: false,
      isSavingConfig: false,
      appDetails: [],
      isConfigJSON: false,
      isArray: false,
      appType: null,
    };
  },
  mounted() {
    if (this.isSlaveApp) {
      this.slaveId = this.appDetail.slaveId;
      this.apiService = slaveAppsService;
    } else {
      this.apiService = appsService;
    }
    this.appDetails = Array.isArray(this.appDetail) ? this.appDetail : [this.appDetail];
    this.appType = this.appDetails[0].app_type
    this.fetchAppConfig();
    this.isConfigJSON = this.isJSON(this.appType);
  },
  methods: {
    isJSON(appType) {
      return APP_CONFIG_TYPES[appType] === JSON_CONFIG_TYPE;
    },
    getAppDetailType(appDetail) {
      const isArray = Array.isArray(appDetail) && appDetail.length > 0;
      const firstItem = isArray && appDetail[0];
      return { isArray, firstItem };
    },
    fetchAppConfig() {
      const serviceName = this.appDetails[0].service;
      const appType = this.appType;
      const slaveId = this.appDetails[0].slaveId;
      this.isFetchingConfig = true;
      this.apiService['fetchAppConfig'](serviceName, appType, { restrictToast: true, slaveId: slaveId })
        .then(data => {
          this.config = data;
        })
        .catch(err => {})
        .finally(() => {
          this.isFetchingConfig = false;
        });
    },
    saveConfig() {
      this.isSavingConfig = true;
      const serviceDisplayName = this.appDetails[0].display_name;

      Promise.all(this.appDetails.map(appDetail => this.apiService['saveAppConfig'](appDetail.service, this.appType, this.config, appDetail.slaveId)))
        .then(data => {
          this.$toasted.show(`Successfully updated config for ${serviceDisplayName}`);
          this.closeDialog();
        })
        .catch(err => {
          this.$toasted.show(`Failed to update config for ${serviceDisplayName}`, { type: 'error' });
        })
        .finally(() => {
          this.isSavingConfig = false;
        });
    },
  },
};
</script>
