import _cloneDeep from 'lodash/cloneDeep';
import * as localstorageUtil from '../utils/localstorage';
import LOCALSTORAGE from '../constants/localStorage';

const formatSelectedItemsWithSlaveIds = selectedItems => {
  return Object.keys(selectedItems).reduce((acc, service) => {
    acc[service] = (selectedItems[service] || []).map(val => val && val.slaveId).filter(val => val);
    return acc;
  }, {});
};

const filterReleaseMapBySelectedItems = (releaseMap, selectedServicesSlaveIds) => {
  return Object.keys(releaseMap).reduce((acc, serviceName) => {
    const value = Object.keys(releaseMap[serviceName]).reduce((acc, slaveId) => {
      if (selectedServicesSlaveIds[serviceName] && selectedServicesSlaveIds[serviceName].includes(slaveId)) {
        acc.push(releaseMap[serviceName][slaveId]);
      }
      return acc;
    }, []);

    acc[serviceName] = value;
    return acc;
  }, {});
};

export const slaveReleaseList = ({ releaseMap, selectedItems }) => {
  const selectedServicesSlaveIds = formatSelectedItemsWithSlaveIds(selectedItems);
  const filteredReleaseMap = filterReleaseMapBySelectedItems(releaseMap, selectedServicesSlaveIds);
  return Object.keys(filteredReleaseMap).reduce((acc, service) => {
    const slaveAppsReleasesCount = filteredReleaseMap[service].map(val => (val && val.length) || Infinity); // at least one
    const indexOfMinValue = slaveAppsReleasesCount.indexOf(Math.min(...slaveAppsReleasesCount));
    acc[service] = filteredReleaseMap[service][indexOfMinValue] || [];
    return acc;
  }, {});
};

export const setAppSlaveId = (slaveId, slaveDeviceInfo) => {
  localstorageUtil.setItem(LOCALSTORAGE.SLAVE_ID, slaveId);
  localstorageUtil.setItem(LOCALSTORAGE.SLAVE_DEVICE_INFO, JSON.stringify(slaveDeviceInfo));
};

export const getAppSlaveId = () => {
  return localstorageUtil.getItem(LOCALSTORAGE.SLAVE_ID);
};

export const getAppSlaveDeviceInfo = () => {
  const deviceInfo = localstorageUtil.getItem(LOCALSTORAGE.SLAVE_DEVICE_INFO);
  try {
    return JSON.parse(deviceInfo);
  } catch (err) {
    return null;
  }
};

export const removeAppSlaveId = () => {
  localstorageUtil.removeItem(LOCALSTORAGE.SLAVE_ID);
  localstorageUtil.removeItem(LOCALSTORAGE.SLAVE_DEVICE_INFO);
};

function tenPower(n) {
  return Math.pow(10, n - 1);
}

function findVersionCount(versions) {
  const RC_val = 'rc';
  return versions.reduce((acc, version) => {
    const versionNumber = version.toLowerCase().split('v')[1];
    const versions = versionNumber.split('.');
    const numVersions = versions.map(val => {
      if (isNaN(+val) && val.indexOf(RC_val)) {
        let newVal = val.split('-')[0];
        let rcVal = val.split('-rc')[1];

        return +newVal - 0.5 / +rcVal;
      }
      return +val;
    });
    const versionValue = numVersions.reduce((acc, curr, index) => {
      const val = tenPower(numVersions.length - index) * curr;
      acc = acc + val;
      return acc;
    }, 0);
    acc[versionValue] = version;
    return acc;
  }, {});
}

function findMaxVersionValue(versions) {
  const versionCounts = findVersionCount(versions);
  const maxVal = Math.max(...Object.keys(versionCounts));
  return maxVal;
}

export function findMaxVersion(versions) {
  const versionCounts = findVersionCount(versions);
  const maxVal = Math.max(...Object.keys(versionCounts));
  return versionCounts[maxVal];
}

export function sortVersions(versions) {
  const versionCountMap = findVersionCount(versions);
  const versionCounts = Object.keys(versionCountMap).sort((a,b) => b-a); // decending order
  return versionCounts.map(versionCount => versionCountMap[versionCount]);
}

export function getAvailableVersions(allVersions, selectedVersions) {
  const selectedVs = _cloneDeep(selectedVersions);
  const allVs = _cloneDeep(allVersions);
  const maxSelectedVersion = findMaxVersionValue(selectedVs);
  const allVersionsCounts = findVersionCount(allVs);
  const availableVersions = Object.keys(allVersionsCounts).reduce((acc, curr) => {
    if (+curr >= maxSelectedVersion) {
      acc.push(allVersionsCounts[curr]);
    }
    return acc;
  }, []);
  return sortVersions(availableVersions);
}
