<template>
  <v-menu offset-y>
    <template v-slot:activator="{ on, attrs }">
      <v-btn x-small class="mr-2 btn-height-22 white--text" color="downloadColor" v-bind="attrs" v-on="on">
        Download<v-icon x-small right>mdi-download</v-icon>
      </v-btn>
    </template>
    <v-list>
      <v-list-item @click="clickDownload(data, 'json')"> JSON </v-list-item>
      <v-list-item @click="clickDownload(data, 'excel')"> Excel </v-list-item>
      <v-list-item @click="clickDownload(data, 'csv')"> CSV </v-list-item>
    </v-list>
  </v-menu>
</template>
<script>
import { exportJSON, exportCSV, exportExcel } from '../../utils/utils';

export default {
  name: 'export-data',
  props: ['data', 'fileName', 'formatDataService'],
  methods: {
    clickDownload(data, type) {
      let formattedData = data;
      if (typeof this.formatDataService === 'function') {
        formattedData = this.formatDataService(data);
      }
      switch (type) {
        case 'csv':
          return exportCSV(this.fileName, formattedData);
        case 'excel':
          return exportExcel(this.fileName, formattedData);
        case 'json':
          return exportJSON(this.fileName, JSON.stringify(formattedData));
      }
    },
  },
};
</script>

