<template>
  <v-card :loading="isFetchingAppStatuses">
    <v-card-title>
      <v-btn @click="back" icon medium class="mr-1" v-if="multiStepViewConfig[currentView].hasBack">
        <v-icon medium> mdi-arrow-left </v-icon>
      </v-btn>
      <span class="sub-headline"> Bulk Apps | {{ multiStepViewConfig[currentView].title }} </span>
    </v-card-title>
    <v-card-text>
      <v-stepper v-model="currentView">
        <v-stepper-header>
          <v-stepper-step :complete="currentView > 1" step="1"> 1 </v-stepper-step>

          <v-divider></v-divider>
          <v-stepper-step :complete="currentView > 2" step="2"> 2 </v-stepper-step>

          <v-divider></v-divider>

          <v-stepper-step :complete="currentView > 3" step="3"> 3 </v-stepper-step>

          <v-divider></v-divider>

          <v-stepper-step step="4"> 4 </v-stepper-step>
        </v-stepper-header>

        <v-stepper-items>
          <v-stepper-content step="1" class="pa-0">
            <v-alert class="mb-0" v-if="!hasSelectedApps" dense type="info">
              Please select apps from the grid to bulk download.
            </v-alert>
            <v-list flat subheader dense class="text-left">
              <v-subheader>Selected Apps</v-subheader>

              <v-list-item v-for="(app, i) in apps" :key="`selected_apps-${i}`">
                <v-list-item-content>
                  <v-list-item-title v-text="app.displayName"></v-list-item-title>
                  <v-list-item-subtitle v-text="app.version"></v-list-item-subtitle>
                </v-list-item-content>
                <v-list-item-icon>
                  <app-status-chip :state="app.state"></app-status-chip>
                </v-list-item-icon>
              </v-list-item>
            </v-list>
          </v-stepper-content>
          <v-stepper-content step="2">
            <v-form v-model="versionForm" ref="versionForm" lazy-validation>
              <v-row dense class="text-left">
                <v-col cols="12" v-for="(app, i) in apps" :key="`version-form-${renderReleaseCount}-${i}`">
                  <v-row dense>
                    <v-col cols="4">
                      <text-field-display label="Name" :value="app.displayName"></text-field-display>
                    </v-col>
                    <v-col cols="8">
                      <v-combobox
                        :loading="app.loadingReleases"
                        :items="app.releases"
                        label="Select Release Version"
                        hideSelected
                        clearable
                        v-model="app.version"
                        item-text="name"
                        item-value="tag_name"
                        :rules="versionRules"
                      >
                      </v-combobox>
                    </v-col>
                  </v-row>
                </v-col>
              </v-row>
            </v-form>
          </v-stepper-content>
          <v-stepper-content step="3" class="pa-0">
            <div>
              <v-alert class="mb-0" v-if="currentDownloadState === 'DOWNLOAD_IN_PROGRESS'" dense type="info">
                Downloading Selected App(s) <strong>Please Wait!</strong>
              </v-alert>
              <v-alert class="mb-0" v-if="currentDownloadState === 'DOWNLOAD_COMPLETE'" dense type="success">
                Downloading Completed
              </v-alert>
            </div>
            <v-list flat subheader dense class="text-left" v-if="currentDownloadState === 'DOWNLOAD_COMPLETE'">
              <v-subheader>App Download Statuses</v-subheader>

              <v-list-item v-for="(app, i) in downloadStates" :key="`download-statuses-${i}`">
                <v-list-item-content>
                  <v-list-item-title v-text="app.serviceName"></v-list-item-title>
                  <v-list-item-subtitle v-text="app.version"></v-list-item-subtitle>
                </v-list-item-content>
                <v-list-item-icon>
                  <div v-if="app.isDownloading">
                    <span class="font-weight-medium">
                      <v-chip outlined color="downloadColor">Downloading</v-chip>
                    </span>
                  </div>
                  <div v-else-if="app.hasError">
                    <span class="font-weight-medium">
                      <v-chip outlined color="red">Failed</v-chip>
                    </span>
                  </div>
                  <div v-else-if="!app.isDownloading && !app.hasError">
                    <span class="font-weight-medium">
                      <v-chip outlined color="nubeGreen">Downloaded</v-chip>
                    </span>
                  </div>
                </v-list-item-icon>
              </v-list-item>
            </v-list>
          </v-stepper-content>
          <v-stepper-content step="4" class="pa-0">
            <v-list flat subheader dense class="text-left" v-if="currentDownloadState === 'DOWNLOAD_COMPLETE'">
              <v-list-item-group v-if="successInstallApps.length > 0">
                <v-subheader>Installation Completed Apps</v-subheader>

                <v-list-item v-for="(successApps, i) in successInstallApps" :key="`install-complete-${i}`">
                  <v-list-item-content>
                    <v-list-item-title v-text="successApps.service"></v-list-item-title>
                    <v-list-item-subtitle v-text="successApps.version"></v-list-item-subtitle>
                  </v-list-item-content>
                  <v-list-item-icon>
                    <div>
                      <span class="font-weight-medium">
                        <v-chip outlined color="blue">Installed</v-chip>
                      </span>
                    </div>
                  </v-list-item-icon>
                </v-list-item>
              </v-list-item-group>
              <v-list-item-group v-if="failureInstallApps.length > 0">
                <v-subheader> Installation Failed Apps</v-subheader>
                <v-list-item v-for="(failedApps, i) in failureInstallApps" :key="`installation-failed-${i}`">
                  <v-list-item-content>
                    <v-list-item-title v-text="failedApps.service"></v-list-item-title>
                    <v-list-item-subtitle v-text="failedApps.version"></v-list-item-subtitle>
                    <v-list-item-subtitle v-text="failedApps.error"></v-list-item-subtitle>
                  </v-list-item-content>
                  <v-list-item-icon>
                    <div>
                      <span class="font-weight-medium">
                        <v-chip outlined color="red">Failed</v-chip>
                      </span>
                    </div>
                  </v-list-item-icon>
                </v-list-item>
              </v-list-item-group>
            </v-list>
          </v-stepper-content>
        </v-stepper-items>
      </v-stepper>
    </v-card-text>
    <v-card-actions>
      <v-card-actions>
        <v-btn
          x-small
          class="btn-height-22 white--text"
          color="primary"
          @click="next"
          :loading="multiStepViewConfig[currentView].loader"
          :disabled="multiStepViewConfig[currentView].disabled"
          v-if="multiStepViewConfig[currentView].hasForward"
        >
          {{
            typeof multiStepViewConfig[currentView].buttonLabel === 'function'
              ? multiStepViewConfig[currentView].buttonLabel(serviceStatus)
              : multiStepViewConfig[currentView].buttonLabel
          }}
        </v-btn>
        <v-btn
          small
          color="pink"
          :disabled="multiStepViewConfig[currentView].disabled"
          v-if="multiStepViewConfig[currentView].hasCancelInstallation"
          @click="cancelInstallation"
          text
        >
          Cancel Install
        </v-btn>

        <v-btn small text v-if="multiStepViewConfig[currentView].hasCancel" @click="cancel"> Close </v-btn>
      </v-card-actions>
    </v-card-actions>
  </v-card>
</template>
<script>
import AppStatusChip from '../Services/AppStatusChip';
import _zipObject from 'lodash/zipObject';
import TextFieldDisplay from '../Common/TextFieldDisplay';
import * as appsService from '../../services/appsService';

const DOWNLOAD_IN_PROGRESS = 'DOWNLOAD_IN_PROGRESS';
const DOWNLOAD_COMPLETE = 'DOWNLOAD_COMPLETE';

export default {
  props: [
    'selectedApps',
    'clearSelectedApps',
    'closeDialog',
    'fetchDownloadAppState',
    'currentDownloadState',
    'appDownloadStates',
    'clearDownloadAppStateInterval',
    'deleteDownloadState',
  ],
  components: {
    AppStatusChip,
    TextFieldDisplay,
  },
  data() {
    return {
      versionForm: false,
      renderReleaseCount: 0,
      isFetchingAppStatuses: false,
      successInstallApps: [],
      failureInstallApps: [],
      currentView: 1,
      refreshTime: 10000,
      versionRules: [value => !!value || 'Version No. is required.'],
      multiStepViewConfig: {
        1: {
          hasBack: false,
          hasForward: true,
          title: 'Selected Apps',
          buttonLabel: 'Continue',
          loader: false,
          disabled: false,
          hasCancel: true,
        },
        2: {
          hasBack: true,
          hasForward: true,
          title: 'Download',
          buttonLabel: 'Download App',
          loader: false,
          disabled: false,
          hasCancel: true,
        },
        3: {
          hasBack: false,
          hasForward: true,
          title: 'Download',
          buttonLabel: 'Install',
          hasCancelInstallation: true,
          loader: false,
          disabled: true,
          hasCancel: true,
          cancelTitle: 'Close',
        },
        4: {
          hasBack: false,
          hasForward: true,
          title: 'Installation Complete',
          buttonLabel: 'Ok',
          loader: false,
          disabled: false,
          hasCancel: false,
        },
      },
    };
  },
  updated() {
    if (!this.hasSelectedApps && !this.currentDownloadState && this.currentDownloadState !== DOWNLOAD_COMPLETE) {
      this.currentView = 1;
      this.multiStepViewConfig[1].disabled = true;
    } else {
      this.multiStepViewConfig[1].disabled = false;
    }

    if (this.currentDownloadState === DOWNLOAD_IN_PROGRESS || !this.currentDownloadState) {
      this.multiStepViewConfig[3].disabled = true;
    } else {
      this.multiStepViewConfig[3].disabled = false;
    }
  },
  watch: {
    currentDownloadState: function (newVal, oldVal) {
      this.showDownloadStateView(newVal);
    },
  },
  created() {
    this.showDownloadStateView(this.currentDownloadState);
  },
  computed: {
    currentBulkAppDownloadState() {
      return this.$store.state.currentBulkAppDownloadState;
    },
    hasSelectedApps() {
      return this.selectedApps.length > 0;
    },
    apps() {
      return this.selectedApps.map(app => ({
        serviceName: app.service,
        state: app.state,
        version: app.version,
        latestVersion: app.latest_version,
        minVersion: app.min_support_version,
        displayName: app.display_name,
        loadingReleases: false,
        releases: [],
      }));
    },
    downloadStates() {
      if (Array.isArray(this.appDownloadStates) && this.appDownloadStates.length > 0) {
        this.currentView = 3;
      }
      return (this.appDownloadStates || []).map(downloadState => ({
        isDownloading: !downloadState.download,
        version: downloadState.version,
        serviceName: downloadState.service,
        hasError: !!downloadState.error,
        errorMessage: downloadState.error,
      }));
    },
  },
  created() {
    this.fetchAppsReleases();
  },
  methods: {
    back() {
      this.currentView = this.currentView - 1;
    },
    spinLoader(value) {
      this.multiStepViewConfig[this.currentView].loader = value;
    },
    showDownloadStateView(currentDownloadState) {
      switch (currentDownloadState) {
        case DOWNLOAD_IN_PROGRESS:
          this.currentView = 3; // Download Waiting screen
          break;
        case DOWNLOAD_COMPLETE:
          this.currentView = 3; // Show the download report page
          this.clearDownloadAppStateInterval();
          break;
      }
    },
    fetchAppsReleases() {
      const serviceNames = this.selectedApps.map(val => val.service);

      this.apps.forEach(app => {
        app.loadingReleases = true;
      });

      return appsService
        .fetchAppsReleases(serviceNames)
        .then(appVersions => {
          this.apps.forEach((app, index) => {
            app.releases = appVersions[index];
          });
        })
        .catch(() => {
          this.$toasted.show('Error fetching available releases');
        })
        .finally(() => {
          this.apps.forEach(app => {
            app.loadingReleases = false;
          });
          this.renderReleaseCount++;
        });
    },
    disableInstallationButton() {
      this.multiStepViewConfig[3].disabled = true;
    },
    bulkDownloadApps() {
      this.currentView = 3;
      this.disableInstallationButton();
      const payload = this.apps.map(app => ({ version: app.version, service: app.serviceName }));
      this.spinLoader(true);
      const donwloadPromise = appsService
        .bulkDownloadApps(payload)
        .then(() => {
          this.spinLoader(false);
          this.fetchDownloadAppState();
        })
        .catch(err => {
          this.spinLoader(false);
        });

      return donwloadPromise;
    },
    async next(step) {
      const currentView = this.currentView;
      switch (currentView) {
        case 1:
          this.stopAndContinue();
          break;
        case 2:
          this.download();
          break;
        case 3:
          this.install();
          break;
        case 4:
          this.continue();
          break;
        default:
          this.currentView++;
          break;
      }
    },
    stopAndContinue() {
      this.currentView++;
    },
    download() {
      if (!this.$refs.versionForm.validate()) {
        return;
      }
      this.bulkDownloadApps();
    },
    install() {
      const installPayload = this.downloadStates
        .filter(val => !val.hasError)
        .map(val => ({
          service: val.serviceName,
          version: val.version,
        }));
      this.spinLoader(true);
      return appsService
        .bulkInstallApps(installPayload)
        .then(data => {
          this.spinLoader(false);
          this.currentView = 4; // Installation complete
          this.successInstallApps = data.filter(val => val.installation);
          this.failureInstallApps = data.filter(val => !val.installation);
        })
        .catch(err => {
          this.spinLoader(false);
          this.deleteDownloadState();
          this.$toasted.show('Unexpected error occured while bulk installing apps. Please try again');
        });
    },
    continue() {
      this.resetDialog();
    },
    cancel() {
      this.fetchDownloadAppState();
      this.closeDialog();
    },
    cancelInstallation() {
      this.resetDialog();
    },
    resetDialog() {
      this.versionForm = false;
      this.renderReleaseCount = 0;
      this.isFetchingAppStatuses = false;
      this.currentView = 1;
      this.multiStepViewConfig = {
        1: {
          hasBack: false,
          hasForward: true,
          title: 'Selected Apps',
          buttonLabel: 'Continue',
          loader: false,
        },
        2: {
          hasBack: true,
          hasForward: true,
          title: 'Download',
          buttonLabel: 'Download App',
          loader: false,
        },
        3: {
          hasBack: false,
          hasForward: true,
          title: 'Download',
          buttonLabel: 'Install',
          loader: false,
        },
        4: {
          hasBack: false,
          hasForward: true,
          title: 'Installation Complete',
          buttonLabel: 'Ok',
          loader: false,
        },
      };
      this.deleteDownloadState();
      this.fetchAppsReleases();
      this.clearSelectedApps();
      this.closeDialog(true, { unmount: true });
    },
  },
};
</script>
